* {
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 0.25px;
}
hr {
    margin: 20px auto; /* Margine automatico ai lati per centrarlo */
    border: 2px solid #e40520; /* Verde */
}
.container {
    width: 100%;
}
.title {
    font-family: "Open Sans", sans-serif;
    font-weight: 800;
    font-style: normal;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.text-justify {
    text-align: justify;
}

.bg-nav {
   background-color: #212529;
}

.bg-black {
    background-color: #161616;
}

.social{
    color: #212529;
}

.clickable {
    cursor: pointer;
}

.btn-lg {
    color: #ffffff;
    background-color: #e40520;
    border: none;
    transform: 0.3 ease-in-out;
}

.btn-lg:hover {
    background-color: #ec2239;
    color: #ffffff;
}

.btn-lg:active {
    background-color: #990012 !important;
    color: #ffffff;
}

.bg-main {
    background: linear-gradient(to bottom right, #e40520, #990012);
    color: #ffffff !important;
}

.bg-dark-grey {
    background-color: rgb(31, 31, 31);
}

.caption {
    color: #0a822e;
}

.img-service {
    opacity: 1;
    transition: ease-in-out 0.3s;
}

/* Mostra il testo al passaggio del mouse */
.img-service:hover {
    opacity: 1;
}

/* Aggiunge un velo scuro sopra l'immagine quando si fa hover */
.img-service {
    position: relative;
    overflow: hidden;
    width: 100%;
    object-fit: cover;
}

.square {
    aspect-ratio: 1/1;
    object-fit: cover;
}

.square-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.img-service::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    /* Inizialmente trasparente */
    transition: background 0.3s ease-in-out;
}

.img-service:hover::before {
    background: rgba(0, 0, 0, 0.8);
    /* Oscura leggermente l'immagine */
}

a .service {
    color: white;
}

.bg-footer {
    background: linear-gradient(to bottom right, rgb(14, 14, 14), black);
    color: #ffffff !important;
}
